




























































import { Component, Vue } from 'vue-property-decorator';
import Result from './Result.vue';
import Evaluator from './Evaluator.vue';
import 'codemirror/mode/clike/clike.js'
import 'codemirror/theme/material.css'

@Component({
  components: {
    Result,
    Evaluator
  },
})

export default class Editor extends Vue {
  filename = "File.no"
  history: Array<object> = []
  command = ""

  input = ""
  output: Array<[string, string]> = []

  status = "success"
  show = false
  mode = ""

  inputOptions = {
    tabSize: 2,
    mode: 'text/x-java',
    theme: 'material',
    lineNumbers: true,
    lineWrapping: true
  }

  get disabled() {
    return this.status === "secondary"
  }
  get icon() {
    switch (this.status) {
      case "danger": return "exclamation-circle"
      case "success": return "paper-plane"
      default: return ""
    }
  }

  get wrongFileName() {
    return !/[a-zA-Z_]+\.no/.test(this.filename)
  }

  async compile() {
    try {
      this.status = "secondary"
      const response = await fetch('http://localhost:8077/compile', {
        method: 'POST',
        headers: {
          "Content-Type": "application/json"
        },
        body: JSON.stringify({
          source: this.input,
          filename: this.filename.slice(0, -3)
        })
      })
      const output = (await response.json()).messages
      try {
        this.output = JSON.parse(output)
      } catch {
        this.output = output
      }
      this.status = "success"
    } catch (err) {
      this.status = "danger"
      console.error(err)
    }
  }

  async execute(command: string) {
    try { 
      const response = await fetch('http://localhost:8077/evaluate', {
        method: 'POST',
        headers: {
          "Content-Type": "application/json"
        },
        body: JSON.stringify({
          filename: command
        })
      })
      const output = (await response.json()).output 
      this.history.push({
        command: command + ".main()",
        output: {
          type: ( output === "Class not found."  ? "error" : "message"),
          message: output
        }
      })
    } catch (err) {
      console.error(err)
    }
  }

  clearInput() {
    this.mode = "input"
    this.show = true
  }
  clearOutput() {
    this.mode = "output"
    this.show = true
  }
  clear() {
    this.show = false
    setTimeout(() => this.mode = "", 300)
    if (this.mode == "input") this.input = ""
    else this.output = []
  }

  executeCommand() {
    const matches = /([a-zA-Z_]*)\.main\(\)/.exec(this.command)
    if (this.command.trim().length == 0) {
      this.history.push({
        command: "",
        output: {
          type: "message",
          message: ""
        }
      })
    }
    else if (this.command == "clear") {
      this.history = []
    } else if (matches && matches.length > 1) {
      this.execute(matches[1])
    } else if (this.command == "help") {
      this.history.push({
        command: this.command,
        output: {
          type: "success",
          message: "Hello! These are the possible commands:\n" + 
                   "- <filename>.main() : execute the file named <filename> (without the .no termination).\n" + 
                   "  Example: File.main()\n" + 
                   "- clear : clear the evaluation console.\n" + 
                   "- help : show this help message."
        }
      })
    } else {
      this.history.push({
        command: this.command,
        output: {
          type: "error",
          message: "Invalid command."
        }
      })
    }
    this.command = ""
  }
}
